import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'BlankFilling',
    component: Home
  },
  {
    path: '/singlechoice',
    name: 'SingleChoice',
    component: () => import(/* webpackChunkName: "about" */ '../components/SingleChoice.vue')
  },
  {
    path: '/trueorfalse',
    name: 'TrueOrFalse',
    component: () => import(/* webpackChunkName: "about" */ '../components/TrueOrFalse.vue')
  },
  {
    path: '/shortanswer',
    name: 'ShortAnswer',
    component: () => import(/* webpackChunkName: "about" */ '../components/ShortAnswer.vue')
  },
]

const router = new VueRouter({
  //mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
